<template>
  <div>
    <div class="border rounded" style="height:80px">
      <div class="pt-3">Средняя скорость</div>
      <div>
        <!-- <b-spinner v-bind:class="classSpinner" label="Loading..."></b-spinner> -->
      </div>
      <div v-bind:class="classCnt" style="font-size:20px; color:red">
        {{ averVelocity }} км/ч
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "averageSpeed",
  props: {
    type: String,
    dataLocation: {}
  },
  data() {
    return {
      averVelocity: Number,
      classSpinner: "",
      classCnt: "d-none"
    };
  },
  watch: {
    dataLocation: function() {
      this.classSpinner = this.dataLocation.classSpinner;
      this.classCnt = this.dataLocation.classCnt;

      if (this.type == "today") {
        this.averVelocity = this.dataLocation.velocityToday;
      }

      if (this.type == "yesterday") {
        this.averVelocity = this.dataLocation.velocityYesterday;
      }

      if (this.type == "month") {
        this.averVelocity = this.dataLocation.velocityMonth;
      }

      if (this.type == "year") {
        this.averVelocity = this.dataLocation.velocityYear;
      }
    }
  }
};
</script>

<style scoped></style>
