<template>
  <div class="m-2 border rounded">
    {{ dataLocation.cnt1ByTimeToday1 }}
    <div class="mb-3">Распределение по времени</div>
    <b-container>
      <b-row>
        <b-col cols="12">
          <div>
            <b-button variant="light">
              C 00 до 07 -&nbsp;
              <b-badge variant="danger">{{ time1 }}</b-badge>
            </b-button>
          </div>
        </b-col>
        <b-col cols="12">
          <div class="text-center mt-2">
            <b-button variant="light">
              C 07 до 13 -&nbsp;
              <b-badge variant="warning">{{ time2 }}</b-badge>
            </b-button>
          </div>
        </b-col>
        <b-col cols="12">
          <div class="text-center mt-2">
            <b-button variant="light">
              C 13 до 15 -&nbsp;
              <b-badge variant="danger">{{ time3 }}</b-badge>
            </b-button>
          </div>
        </b-col>
        <b-col cols="12">
          <div class="text-center mt-2">
            <b-button variant="light">
              C 15 до 21 -&nbsp;
              <b-badge variant="warning">{{ time4 }}</b-badge>
            </b-button>
          </div>
        </b-col>
        <b-col cols="12">
          <div class="text-center mt-2 mb-3">
            <b-button variant="light">
              C 21 до 00 -&nbsp;
              <b-badge variant="danger">{{ time5 }}</b-badge>
            </b-button>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "aircraftCountPeriods",
  props: {
    type: String,
    dataLocation: {}
  },
  data() {
    return {
      time1: 0,
      time2: 0,
      time3: 0,
      time4: 0,
      time5: 0
    };
  },
  watch: {
    dataLocation: function() {
      if (this.type == "today") {
        this.time1 = this.dataLocation.cntByTimeToday1;
        this.time2 = this.dataLocation.cntByTimeToday2;
        this.time3 = this.dataLocation.cntByTimeToday3;
        this.time4 = this.dataLocation.cntByTimeToday4;
        this.time5 = this.dataLocation.cntByTimeToday5;
      }

      if (this.type == "yesterday") {
        this.time1 = this.dataLocation.cntByTimeYesterday1;
        this.time2 = this.dataLocation.cntByTimeYesterday2;
        this.time3 = this.dataLocation.cntByTimeYesterday3;
        this.time4 = this.dataLocation.cntByTimeYesterday4;
        this.time5 = this.dataLocation.cntByTimeYesterday5;
      }

      if (this.type == "month") {
        this.time1 = this.dataLocation.cntByTimeMonth1;
        this.time2 = this.dataLocation.cntByTimeMonth2;
        this.time3 = this.dataLocation.cntByTimeMonth3;
        this.time4 = this.dataLocation.cntByTimeMonth4;
        this.time5 = this.dataLocation.cntByTimeMonth5;
      }

      if (this.type == "year") {
        this.time1 = this.dataLocation.cntByTimeYear1;
        this.time2 = this.dataLocation.cntByTimeYear2;
        this.time3 = this.dataLocation.cntByTimeYear3;
        this.time4 = this.dataLocation.cntByTimeYear4;
        this.time5 = this.dataLocation.cntByTimeYear5;
      }
    }
  }
};
</script>

<style scoped></style>
