        this.cnt = this.dataLocation.cntToday;
      }

      if (this.type == "yesterday") {
        this.cnt = this.dataLocation.cntYesterday;
      }

      if (this.type == "month") {
        this.cnt = this.dataLocation.cntMonth;
      }

      if (this.type == "year") {
        this.cnt = this.dataLocation.cntYear;
      }
    }
