<template>
  <div>
    <div class="border rounded" style="height:80px">
      <div>
        <div class="pt-3">Средняя высота</div>
        <div>
          <!-- <b-spinner v-bind:class="classSpinner" label="Loading..."></b-spinner> -->
        </div>
        <div v-bind:class="classCnt" style="font-size:20px; color:red">
          {{ averLat }} м
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "averageLatitude",
  props: {
    type: String,
    dataLocation: {}
  },
  data() {
    return {
      averLat: Number,
      classSpinner: "",
      classCnt: "d-none"
    };
  },
  watch: {
    dataLocation: function() {
      this.classSpinner = this.dataLocation.classSpinner;
      this.classCnt = this.dataLocation.classCnt;

      if (this.type == "today") {
        this.averLat = this.dataLocation.altitudeToday;
      }

      if (this.type == "yesterday") {
        this.averLat = this.dataLocation.altitudeYesterday;
      }

      if (this.type == "month") {
        this.averLat = this.dataLocation.altitudeMonth;
      }

      if (this.type == "year") {
        this.averLat = this.dataLocation.altitudeYear;
      }
    }
  }
};
</script>

<style scoped></style>
